import React, { Component } from 'react';

class requestACard extends Component {  
  render() {
    return (
      <div className="requestACard">
        <div className = "arrowHolder">
          <button className = "arrowButton">
            <i class="fas fa-arrow-left" onClick = {this.props.history.goBack}/>
          </button>
        </div>
        <h1 className = "PageTitle">Request a Card</h1>
        <p className = "featureComingSoon">Request a card  feature coming soon</p>
      </div>
    );
  }
}

export default requestACard;